<template>
  <a-statistic class="total" title="全年累计接待游客" :precision="2" :value="value">
    <template #suffix>万人次</template>
  </a-statistic>
</template>
<script>
export default {
  data() {
    return {
      value: 444.123
    };
  },
  mounted() {
    this.$unlock();

    setTimeout(() => {
      this.value += 100;
    }, 3000);
  }
};
</script>
<style lang="less" scoped>
.total {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  /deep/ .ant-statistic-title,
  /deep/ .ant-statistic-content {
    color: #60c6ff;
    font-size: 18px;
  }

  /deep/ .ant-statistic-content-value {
    color: #01efd8;
    font-size: 28px;
  }
}
</style>