<template>
  <echart :option="option" />
</template>
<script>
import Echart from "@/components/Echart.vue";

export default {
  props: {
    title: {},
    left: { default: [] },
    right: { default: [] },
    values: { default: [] },
    max: {},
    showIndex: {
      default: false
    }
  },
  mounted() {
    this.$unlock({ monthSelect: true });
  },
  computed: {
    option() {
      return {
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b}: {c} ({d}%)"
        },
        color: ["#00BEFF", "#FDB628", "#EA7148"],
        legend: {
          type: "scroll",
          left: 20,
          right: 20,
          bottom: 0,
          data: ["直接访问", "邮件营销", "联盟广告", "视频广告", "搜索引擎"],
          pageTextStyle: {
            color: "#FFF"
          },
          textStyle: {
            color: "#FFF"
          }
        },
        title: {
          text: this.title,
          x: "center",
          top: "6%",
          textStyle: {
            fontSize: "100%",
            fontWeight: "normal",
            color: "#03D3D3"
          }
        },
        grid: {
          left: "12%",
          right: "12%",
          top: "30%",
          bottom: "10%",
          containLabel: true
        },
        series: [
          {
            name: this.title,
            type: "pie",
            radius: "55%",
            center: ["50%", "50%"],
            data: [
              { value: 335, name: "直接访问" },
              { value: 310, name: "邮件营销" },
              { value: 274, name: "联盟广告" },
              { value: 235, name: "视频广告" },
              { value: 400, name: "搜索引擎" }
            ].sort(function(a, b) {
              return a.value - b.value;
            }),
            roseType: "radius",
            label: {
              color: "rgba(255, 255, 255, 1)"
            },
            labelLine: {
              smooth: 0.2,
              length: 10,
              length2: 20
            },
            itemStyle: {
              shadowBlur: 200,
              shadowColor: "rgba(0, 0, 0, 0.5)"
            },
            animationType: "scale",
            animationEasing: "elasticOut",
            animationDelay: function() {
              return Math.random() * 200;
            }
          }
        ]
      };
    }
  },
  components: {
    Echart
  }
};
</script>