<template>
  <echart :option="option" />
</template>
<script>
import Echart from "@/components/Echart.vue";

export default {
  mounted() {
    this.$unlock();
  },
  data() {
    return {
      option: {
        title: {
          show: false
        },
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b} : {c} ({d}%)"
        },
        legend: {
          bottom: 10,
          left: "center",
          data: ["1天以内", "1-3天", "3天以上"]
        },
        series: [
          {
            name: "事件比例",
            type: "pie",
            radius: "60%",
            center: ["50%", "50%"],
            data: [
              {
                name: "1天以内",
                value: Math.round(Math.random() * 100000)
              },
              {
                name: "1-3天",
                value: Math.round(Math.random() * 100000)
              },
              {
                name: "3天以上",
                value: Math.round(Math.random() * 100000)
              }
            ],
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)"
              }
            }
          }
        ]
      }
    };
  },
  components: {
    Echart
  }
};
</script>