<template>
  <top-bar :title="'过夜游客'" :left="left" :right="right" :max="max" :values="values" />
</template>
<script>
import TopBar from "@/components/charts/TopBar.vue";

export default {
  mounted() {
    this.$getData("/behavior/overnight").then(list => {
      this.list = list;
      this.$unlock();
    });
  },
  data() {
    return {
      list: []
    };
  },
  computed: {
    left() {
      return this.list.map(({ type }) => type);
    },
    values() {
      return this.list.map(({ value }) => Number(value) * 100);
    },
    right() {
      return this.values.map(item => item.toFixed(2) + "%");
    },
    max() {
      return Math.max(...this.values) * 1.2;
    }
  },
  components: {
    TopBar
  }
};
</script>