<template>
  <ring :title="'出行方式占比'" :values="values" />
</template>
<script>
import Ring from "@/components/charts/Ring.vue";

export default {
  mounted() {
    this.$getData("/behavior/transportation").then(list => {
      this.list = list;
      this.$unlock();
    });
  },
  data() {
    return {
      list: []
    };
  },
  computed: {
    values() {
      return this.list.map(({ type, value }) => ({ name: type, value }));
    }
  },
  components: {
    Ring
  }
};
</script>